<script>
// @ is an alias to /src

import AsideApp from '@/components/AsideApp.vue';
import TopApp from '@/components/TopApp.vue';
import axios from 'axios';

export default {
  name: 'CreateProductoApp',
  components: {
    AsideApp,
    TopApp
  },
  data() {
      return {
          str_image: '/assets/default.jpg',
          producto: {
              categoria: '',
              estado: false,
              descuento: false,
              portada: undefined,
              subcategoria: '',
          },
          categorias:[],
          subcategorias: [],
          portada : undefined,
      }
  },
  beforeMount(){
    this.init_categorias();
  },
  methods: {
      uploadImage($event){

          /*  var image;

            if($event.target.files.length >= 1){
                image = $event.target.files[0];
            }

            if(image.size <= 1000000){
               if(image.type == 'image/jpeg'||image.type == 'image/png'||image.type == 'image/webp'||image.type == 'image/jpg'){
                   this.str_image = URL.createObjectURL(image);
                    this.portada = image;
                    this.producto.portada = this.portada;
               }else{
                   this.$notify({
                    group: 'foo',
                    title: 'ERROR',
                    text: 'El recurso debe ser imagen.',
                    type: 'error'
                });
                this.portada = undefined;
               }
            }else{
                this.$notify({
                    group: 'foo',
