            console.log(this.portada);
          
      },

      uploadImage($event){*/

var image;

if($event.target.files.length >= 1){
    image = $event.target.files[0];
}

if(image.size <= 1000000){
   if(image.type == 'image/jpeg'||image.type == 'image/png'||image.type == 'image/webp'||image.type == 'image/jpg'){
       this.str_image = URL.createObjectURL(image);
        this.portada = image;
        this.producto.portada = this.portada;
   }else{
       this.$notify({
        group: 'foo',
        title: 'ERROR',
        text: 'An image must be entered',
        type: 'error'
    });
    this.portada = undefined;
   }
}else{
    this.$notify({
        group: 'foo',
        title: 'ERROR',
        text: 'The image size can be up to 1MB',
        type: 'error'
    });
    this.portada = undefined;
}
console.log(this.portada);

},


      validar(){
            if(!this.producto.titulo){
                this.$notify({
                    group: 'foo',
                    title: 'ERROR',
                    text: 'Enter the product title',
                    type: 'error'
                });
            }else if(!this.producto.categoria){
                this.$notify({
                    group: 'foo',
                    title: 'ERROR',
                    text: 'Select the product category',
                    type: 'error'
                });
            
            }else if(!this.producto.subcategoria){
                this.$notify({
                    group: 'foo',
                    title: 'ERROR',
                    text: 'Select the product subcategory',
                    type: 'error'
                });
            }else if(!this.producto.precio){
                this.$notify({
                    group: 'foo',
                    title: 'ERROR',
                    text: 'Enter the product price',
                    type: 'error'
                });
            }else if(!this.producto.extracto){
                this.$notify({
                    group: 'foo',
                    title: 'ERROR',
                    text: 'Enter the product description',
                    type: 'error'
                });
            }else if(!this.producto.str_variedad){
                this.$notify({
                    group: 'foo',
                    title: 'ERROR',
                    text: 'Enter the product characteristic',
                    type: 'error'
                });
            }else if(this.producto.portada == undefined){
                this.$notify({
                    group: 'foo',
                    title: 'ERROR',
                    text: 'Select a cover image',
                    type: 'error'
                });
            }else{
                this.registro();
            }
          
      },

      registro(){
          var fm = new FormData();
          fm.append('titulo',this.producto.titulo);
          fm.append('categoria',this.producto.categoria);
          fm.append('subcategoria',this.producto.subcategoria);
          fm.append('precio',this.producto.precio);
          fm.append('extracto',this.producto.extracto);
          fm.append('estado',this.producto.estado);
          fm.append('str_variedad',this.producto.str_variedad);
          fm.append('descuento',this.producto.descuento);
          fm.append('portada',this.producto.portada); //IMAGEN

          axios.post(this.$url+'/registro_producto_admin',fm,{
              headers: {
                  'Content-Type': 'multipart/form-data',
                  'Authorization' : this.$store.state.token
              }
          }).then((result)=>{
            if(result.data.message){
                this.$notify({
                    group: 'foo',
                    title: 'ERROR',
                    text: result.data.message,
                    type: 'error'
                });
              }else{
                  this.$notify({
                    group: 'foo',
                    title: 'SUCCESS',
                    text: 'The product was registered.',
                    type: 'success'
                });

                this.$router.push({name:'producto-index'});
              }
          })


      },

      init_categorias(){
        axios.get(this.$url+'/listar_categorias_admin',{
            headers:{
                'Content-Type': 'application/json',
                'Authorization': this.$store.state.token,
            }
        }).then((result)=>{
            
            this.categorias = result.data;
        });
    },
    getsubcategorias(event){
      this.subcategorias = this.categorias.filter(item=>item.categoria.titulo == event.target.value)[0].subcategorias;
        }
  },
}
</script>
